.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

video::-webkit-media-controls {
  border: none !important;
  outline: none !important;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.ant-input::placeholder,
.ant-input-affix-wrapper input::placeholder,
.ant-input-affix-wrapper .ant-input::placeholder,
.custom-input input::placeholder,
.custom-input .ant-input::placeholder {
  color: #ffffff !important;
  opacity: 1 !important;

}


::-webkit-scrollbar {
  width: 4px;
  min-height: 2px;
  max-height: 5px;

}

::-webkit-scrollbar-thumb {
  background-color: #000000;
  opacity: 0.9;

  border-radius: 10px;

}

::-webkit-scrollbar-track {
  background: #292929;
  opacity: 0.4;

}

.scrollbar-left {
  direction: rtl;

}

.scrollbar-left>* {
  direction: ltr;

}

.shadow-custom {
  box-shadow: 1px 2px 3px rgb(179, 179, 179);
}

.scrollbar-hide::-webkit-scrollbar {
  display: none;
}

.scrollbar-hide {
  -ms-overflow-style: none;

  scrollbar-width: none;

}


article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}



ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

*,
body {

  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;
  overflow: hidden;

}

.overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  background-image: url('./image/dark-sunset.jpg');
  z-index: 2;
  filter: blur(13px);
  background-repeat: no-repeat;
  background-size: cover;
}


.login-container {
  position: relative;

  z-index: 3;
  height: 100vh;

  background-image: url('./image/dark-sunset.jpg');


  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: space-between;
  padding-left: 100px;
  color: white;
  font-weight: 300;

}

.login-container>div:first-child {
  max-width: 400px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 100px 0;
  padding-bottom: 70px;
}

.logo span {
  font-family: 'Kaushan Script', cursive;
  font-size: 23px;
}

.logo i {
  color: rgb(255, 255, 255);
  font-size: 34px;
}

.register>div:first-child {
  font-size: 32px;
  max-width: 300px;
  line-height: 1.3;
  margin-bottom: 20px;
}

.register>p {
  font-size: 14px;
  line-height: 1.4;
  color: rgb(197, 202, 206);
  margin-bottom: 24px;
}

.register .social {
  margin-left: -20px;
}

.register .social a {
  margin-left: 20px;
}

.register .social i {
  color: white;
  font-size: 15px;
}

.register .social a:hover i {
  color: red;
}

.form-login {
  width: 45%;
  max-width: 48%;
  height: 100%;
  backdrop-filter: blur(8px);
  background-color: rgba(54, 52, 52, 0.2);

  padding: 100px 40px;
}

.form-login .form-group {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 40px;
}

.form-login .form-group.f-term {
  display: flex;
  flex-direction: row !important;
  align-items: center;
}

.f-term label {
  margin-top: 10px;
  margin-left: 7px;
}

.f-term label a {
  margin-left: 3px;
  color: rgb(228, 220, 220);

  font-size: 13px;
  text-underline-position: under;
}

.f-term label a:hover {
  color: red;
}

.form-login .form .form-group input {
  background-color: transparent;
  outline: none;
  border: none;
  border-bottom: 1px solid rgba(159, 171, 175, 0.534);
  margin-top: 10px;
  color: white;
  font-size: 17px;
}

.form-login .form .form-group input::placeholder {

  color: rgba(255, 255, 255, 0.658);
  opacity: 1;

}

.form-login .form .form-group .form-label {
  color: rgba(255, 255, 255, 0.658);
  font-size: 13px;
}

.form-login .form .heading {
  font-size: 30px;
  margin-top: 7px;
}

.spacer {
  width: 100%;
  height: 50px;
}


.form-login .form .sign-up {
  margin-top: 50px;
  display: flex;
  align-items: center;
}

.form-login .form .sign-up>div {
  position: relative;
}

.form-login .form .sign-up button {
  background-color: rgb(211, 37, 37);
  color: rgb(238, 227, 227);
  width: 160px;
  height: 35px;
  border: none;
  outline: none;
  font-size: 16px;
  display: flex;
  align-items: center;
  padding-left: 25px;

}

.form-login .form .sign-up button:hover {
  background-color: rgb(143, 24, 24);
  cursor: pointer;
}

.form-login .form .sign-up>div i {
  position: absolute;
  top: 50%;
  right: 7%;
  transform: translateY(-50%);
  font-size: 12px;
}


.sign-up a {
  margin-left: 35px;
  color: rgb(190, 189, 187);
  font-size: 14px;
  text-underline-position: under;
}

.sign-up a:hover {
  color: red;
}